	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"I Hate Banks","date":"24 Jul 2002","slug":"/2002-07-24-i-hate-banks/","tags":"Y2002"},"html":"<p>I hate banks... no, let me rephrase that. My employer's customers are banks, which means they pay my salary, so I love banks. So much that I set up a web-based bugtracker so they can report problems with our software directly to us. Except that that their sys admins are paranoid, and they test our system in a firewalled network with no internet access. So they can't access our bugtracker. And they still want to track bugs. And the idea of bringng a notebook with a WiFi card, or even a plain old <i>modem</i> dialed in to T-Online, into their stinking white room hasn't occurred to them. I hate banks.</p>\n<p>Actually, since the customer is always right, I should blame our project manager for not giving me the correct specs, like 'build a web-based bugtracking system than can also be used by users without web access'.</p>"},"next":{"fields":{"title":"We Live Like A Future Minister","date":"22 Jul 2002","slug":"/2002-07-22-we-live-like-a-future-minister/"}},"previous":{"fields":{"title":"Plankton","date":"25 Jul 2002","slug":"/2002-07-25-plankton/"}}},{"node":{"fields":{"title":"We Live Like A Future Minister","date":"22 Jul 2002","slug":"/2002-07-22-we-live-like-a-future-minister/","tags":"Y2002"},"html":"<p>This evening the ARD news magazine <a href=\"http://www.mdr.de/fakt\">FAKT</a> had a segment on working women in Germany. One family they showed was much like ours. The career mother has a better job than the father, who has to be be somewhat flexible, but both work full time while the child is cared for by a <i>Tagesmutter</i>. Only at the end did I realize they were showing Stoiber's advisor on family and social policy <a href=\"http://www.papascott.de/2002/07/16/1810.php#001810\">Katherina Reiche</a>, about whom I posted a few days ago. So I guess we shouldn't be jealous of the lifestlyes of politicians, although it would still be nice to get some <a href=\"http://kleist.dwelle.de/english/current_affairs/currentaffairs1.html\">money from lobbyists</a>.</p>"},"next":{"fields":{"title":"Strange Things That Break","date":"21 Jul 2002","slug":"/2002-07-21-strange-things-that-break/"}},"previous":{"fields":{"title":"I Hate Banks","date":"24 Jul 2002","slug":"/2002-07-24-i-hate-banks/"}}},{"node":{"fields":{"title":"Strange Things That Break","date":"21 Jul 2002","slug":"/2002-07-21-strange-things-that-break/","tags":"Y2002"},"html":"<p>Our ISDN router, an old 100 MHz machine running <a href=\"http://www.fli4l.de/\">Linux on a floppy</a>, wouldn't restart last night. I spent 2 hours to figure out that it was the <i>video</i> card that went south. Strange, because it never has a monitor attached. The video card is only there because the BIOS insists on having it.</p>"},"next":{"fields":{"title":"It's Raining Men","date":"21 Jul 2002","slug":"/2002-07-21-its-raining-men/"}},"previous":{"fields":{"title":"We Live Like A Future Minister","date":"22 Jul 2002","slug":"/2002-07-22-we-live-like-a-future-minister/"}}}],"pathPrefix":"","first":false,"last":false,"index":868,"pageCount":1101}}

